.App:has(> #home) {
  margin-top: 5rem;
  overflow: hidden;
}
.App:has(> #home) footer {
  margin-top: 0;
}
.bg-image {
  background-image: url("../../../public/assets/images/bg-primery.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  opacity: 0.9999;
  width: 100vw;
  height: 100vh;
  color: #fff;

  display: flex;
  flex-direction: column;
  justify-content: center;
  position: relative;
}
.blur-area {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  z-index: -1;
}
h1.intro {
  font-size: 4em;
  font-weight: 600;
}
.intro {
  text-align: left;
  margin-left: 1rem;
}
.sample-products {
  position: absolute;
  right: 15%;
  top: 0;
  margin-block: 5%;
  display: flex;
  gap: 1rem;
  align-items: center;
  justify-content: center;
}

@media(max-width:1260px){
  .sample-products{
    right: 5%;
  }
}
@media(max-width:1100px){
  .bg-image{
    justify-content: start;
  }
  .sample-products{
    width: 100%;
  }
}

@media(max-width:880px){
    .bg-image{
    justify-content: center;
  }

  .sample-products{
    position: static;
    margin-top: 6rem
  }
  h1.intro {
  font-size: 3em;
}
}